      infoText2: "\u0642\u0645 \u0628\u062A\u062E\u0632\u064A\u0646 \u0627\u0644\u0631\u0645\u0648\u0632 \u0641\u064A \u0645\u0643\u0627\u0646 \u0622\u0645\u0646 \u0648\u062D\u0627\u0641\u0638 \u0639\u0644\u0649 \u0633\u0631\u064A\u062A\u0647\u0627. \u064A\u0645\u0643\u0646\u0643 \u062A\u062C\u062F\u064A\u062F \u0627\u0644\u0631\u0645\u0648\u0632 \u0627\u0644\u0623\u062D\u062A\u064A\u0627\u0637\u064A\u0629 \u0627\u0630\u0627 \u0643\u0646\u062A \u062A\u0634\u0643 \u0623\u0646 \u062D\u0633\u0627\u0628\u0643 \u0645\u0639\u0631\u0636 \u0644\u0644\u062E\u0637\u0631",
      subtitle__codelist: "\u0642\u0645 \u0628\u062A\u062E\u0632\u064A\u0646 \u0627\u0644\u0631\u0645\u0648\u0632 \u0641\u064A \u0645\u0643\u0627\u0646 \u0622\u0645\u0646 \u0648\u0627\u0628\u0642\u064A\u0647\u0627 \u0633\u0631\u0627.",
