      successSubtitle: "\u064A\u0645\u0643\u0646\u0643 \u0623\u0633\u062A\u0639\u0645\u0627\u0644 \u0648\u0627\u062D\u062F\u0629 \u0645\u0646 \u0647\u0630\u0647 \u0627\u0644\u0631\u0645\u0648\u0632 \u0644\u062A\u0633\u062C\u064A\u0644 \u0627\u0644\u062F\u062E\u0648\u0644 \u0625\u0644\u0649 \u062D\u0633\u0627\u0628\u0643, \u0627\u0630\u0627 \u0641\u0642\u062F\u062A \u0627\u0644\u0648\u0635\u0648\u0644 \u0625\u0644\u0649 \u062C\u0647\u0627\u0632 \u0627\u0644\u0645\u0635\u0627\u062F\u0642\u0629 \u0627\u0644\u062E\u0627\u0635 \u0628\u0643",
