      infoText2: "\u0414\u0440\u044A\u0436\u0442\u0435 \u0440\u0435\u0437\u0435\u0440\u0432\u043D\u0438\u0442\u0435 \u043A\u043E\u0434\u043E\u0432\u0435 \u0432 \u0442\u0430\u0439\u043D\u0430 \u0438 \u0433\u0438 \u0441\u044A\u0445\u0440\u0430\u043D\u044F\u0432\u0430\u0439\u0442\u0435 \u0441\u0438\u0433\u0443\u0440\u043D\u043E. \u041C\u043E\u0436\u0435\u0442\u0435 \u0434\u0430 \u0433\u0435\u043D\u0435\u0440\u0438\u0440\u0430\u0442\u0435 \u043D\u043E\u0432\u0438 \u0440\u0435\u0437\u0435\u0440\u0432\u043D\u0438 \u043A\u043E\u0434\u043E\u0432\u0435, \u0430\u043A\u043E \u043F\u043E\u0434\u043E\u0437\u0438\u0440\u0430\u0442\u0435, \u0447\u0435 \u0442\u0435 \u0441\u0430 \u0431\u0438\u043B\u0438 \u043A\u043E\u043C\u043F\u0440\u043E\u043C\u0435\u0442\u0438\u0440\u0430\u043D\u0438.",
