      successMessage: "\u0420\u0435\u0437\u0435\u0440\u0432\u043D\u0438\u0442\u0435 \u043A\u043E\u0434\u043E\u0432\u0435 \u0441\u0430 \u0430\u043A\u0442\u0438\u0432\u0438\u0440\u0430\u043D\u0438. \u041C\u043E\u0436\u0435\u0442\u0435 \u0434\u0430 \u0438\u0437\u043F\u043E\u043B\u0437\u0432\u0430\u0442\u0435 \u0435\u0434\u0438\u043D \u043E\u0442 \u0442\u044F\u0445, \u0437\u0430 \u0434\u0430 \u0432\u043B\u0435\u0437\u0435\u0442\u0435 \u0432 \u0430\u043A\u0430\u0443\u043D\u0442\u0430 \u0441\u0438, \u0430\u043A\u043E \u0437\u0430\u0433\u0443\u0431\u0438\u0442\u0435 \u0434\u043E\u0441\u0442\u044A\u043F\u0430 \u0434\u043E \u0443\u0441\u0442\u0440\u043E\u0439\u0441\u0442\u0432\u043E\u0442\u043E \u0441\u0438 \u0437\u0430 \u0443\u0434\u043E\u0441\u0442\u043E\u0432\u0435\u0440\u044F\u0432\u0430\u043D\u0435. \u0412\u0441\u0435\u043A\u0438 \u043A\u043E\u0434 \u043C\u043E\u0436\u0435 \u0434\u0430 \u0441\u0435 \u0438\u0437\u043F\u043E\u043B\u0437\u0432\u0430 \u0441\u0430\u043C\u043E \u0432\u0435\u0434\u043D\u044A\u0436.",
